@font-face {
  font-family: 'SFProDisplay-Regular', sans-serif;
  src: url("../fonts/SFProDisplay-Regular.woff2") format("woff2"), url("../fonts/SFProDisplay-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'SFProDisplay-Medium', sans-serif;
  src: url("../fonts/SFProDisplay-Medium.woff2") format("woff2"), url("../fonts/SFProDisplay-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'SFProDisplay-Semibold', sans-serif;
  src: url("../fonts/SFProDisplay-Semibold.woff2") format("woff2"), url("../fonts/SFProDisplay-Semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: 'SFProDisplay-Bold', sans-serif;
  src: url("../fonts/SFProDisplay-Bold.woff2") format("woff2"), url("../fonts/SFProDisplay-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*, *::before, *::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  color: #2F3035;
  font-family: "SFProDisplay-Regular", sans-serif;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400; }

a {
  text-decoration: none;
  color: inherit; }

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

.container {
  max-width: 1200px;
  padding: 0 15px;
  margin: 0 auto; }

.header__top-inner {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  min-height: 122px;
  font-family: "SFProDisplay-Bold", sans-serif;
  font-weight: 700; }

.menu__item {
  display: inline-block; }

.menu__item + .menu__item {
  padding-left: 50px; }

.header__box {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.header__adress {
  margin-right: 60px;
  width: 265px;
  padding-left: 30px;
  position: relative; }

.header__adress::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  left: 0;
  top: -10px;
  background-image: url("../img/adress-icon.svg"); }

.user__list {
  margin-right: 15px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.user__item {
  padding-right: 10px; }

.basket {
  position: relative; }

.basket__num {
  position: absolute;
  font-size: 10px;
  font-weight: 500;
  line-height: 20px;
  width: 20px;
  height: 20px;
  color: #FFFFFF;
  background-color: #1C62CD;
  border-radius: 50%;
  text-align: center;
  bottom: -15px;
  right: -14px; }

.header__bottom {
  padding-bottom: 78px; }

.menu__categories {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  background-color: #F0F0F4;
  min-height: 53px; }

.menu__categories__item {
  position: relative; }

.menu__categories__link {
  -webkit-transition: all .3s;
  transition: all .3s;
  font-weight: 400;
  padding: 15px 0 14px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%); }

.menu__categories__link::before {
  content: "";
  position: absolute;
  width: 0;
  right: 0;
  left: auto;
  bottom: -2px;
  background-color: #1C62CD;
  height: 3px;
  -webkit-transition: all .5s;
  transition: all .5s; }

.menu__categories__link:hover::before {
  width: 100%;
  right: auto;
  left: 0; }

.menu__categories__link:hover {
  color: #2F3035;
  font-size: 16px; }
